body {
  font-family: 'Grandstander', cursive;
  background-image: linear-gradient(100deg, #c5ab36, #d18035);
}

li {
  list-style: none;
  background-color: #0d9b6c;
  border-radius: 10px;
  padding: 1%;
  margin: 3% auto;
  width: 50%;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(71, 52, 71, 1);
  -moz-box-shadow: 10px 10px 5px 0px rgba(71, 52, 71, 1);
  box-shadow: 10px 10px 5px 0px rgba(71, 52, 71, 1);
  animation-duration: 2s;
  animation-name: slidein;
}
li:hover {
  background-color: #27033d;
  color: #dfcdda;
}
h5 {
  font-size: 1.1rem;
}
@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}
h1,
h2 {
  text-align: center;
}

@media screen and (max-width: 1000px) {
  li {
    width: 90%;
  }
}
